.container {
  padding-left: 20px;
  padding-right: 20px;
  margin: 0 auto;

  @media screen and (min-width: $mobile) {
    width: $mobile;
  }

  @media screen and (min-width: $tablet) {
    width: $tablet;
    padding-left: 34px;
    padding-right: 34px;
  }

  @media screen and (min-width: $desktop) {
    width: $desktop;
    padding-left: 115px;
    padding-right: 115px;
  }

  &--no-padding {
    padding-left: 0;
    padding-right: 0;
  }
}
